"use client";
import React, { useState } from "react";
import {
  motion,
  AnimatePresence,
  useScroll,
  useMotionValueEvent,
} from "framer-motion";
import { cn } from "@/utils/cn";
import { handleTo } from '@/utils';

export const FloatingNav = ({
  navItems,
  className,
}: {
  navItems: {
    name: string;
    link: string;
    icon?: JSX.Element;
  }[];
  className?: string;
}) => {
  const { scrollYProgress } = useScroll();

  const [visible, setVisible] = useState(false);

  useMotionValueEvent(scrollYProgress, "change", (current) => {
    // Check if current is not undefined and is a number
    if (typeof current === "number") {
      let direction = current! - scrollYProgress.getPrevious()!;

      if (scrollYProgress.get() < 0.05) {
        setVisible(false);
      } else {
        if (direction < 0) {
          setVisible(true);
        } else {
          setVisible(false);
        }
      }
    }
  });

  return (
    <AnimatePresence mode="wait">
      <motion.div
        initial={{
          opacity: 1,
          y: -100,
        }}
        animate={{
          y: visible ? 0 : -100,
          opacity: visible ? 1 : 0,
        }}
        transition={{
          duration: 0.2,
        }}
        className={cn(
          "fixed top-2 md:top-10 left-0 z-[5000] w-full px-4",
          className
        )}
      >
        <div className="flex max-w-fit inset-x-0 mx-auto border rounded-lg shadow-[0px_2px_3px_-1px_rgba(0,0,0,0.1),0px_1px_0px_0px_rgba(25,28,33,0.02),0px_0px_0px_1px_rgba(25,28,33,0.08)] px-10 py-5 items-center justify-center space-x-4 border-white/[0.2] bg-black-100/[0.7] backdrop-blur-sm">
          {navItems.map((navItem: any, idx: number) => (
            <button 
              key={`link=${idx}`}
              type="button" 
              className="inline-flex cursor-pointer relative dark:text-neutral-50 items-center space-x-1 text-neutral-600 dark:hover:text-purple hover:text-purple transition-colors"
              onClick={() => handleTo(navItem.link)}
            >
              <span className="block sm:hidden">{navItem.icon}</span>
              <span className="text-sm !cursor-pointer">{navItem.name}</span>
            </button>
            // <Link
            //   key={`link=${idx}`}
            //   href={navItem.link}
            //   className={cn(
            //     "relative dark:text-neutral-50 items-center flex space-x-1 text-neutral-600 dark:hover:text-purple hover:text-purple transition-colors"
            //   )}
            // >
            //   <span className="block sm:hidden">{navItem.icon}</span>
            //   <span className="text-sm !cursor-pointer">{navItem.name}</span>
            // </Link>
          ))}
        </div>
      </motion.div>
    </AnimatePresence>
  );
};
